// components/CalendarView.tsx
'use client'

import * as React from 'react';
import { 
  format, addMonths, subMonths, startOfMonth, endOfMonth, 
  startOfWeek, endOfWeek, eachDayOfInterval, getDay 
} from 'date-fns';
import { SimpleTransaction } from '@/lib/forecast';
import { Button } from '@/components/ui/button';
import { ToggleGroup, ToggleGroupItem } from '@/components/ui/toggle-group';
import { ChevronLeft, ChevronRight } from 'lucide-react';

interface CalendarViewProps {
  startingBalance: number;
  transactions: SimpleTransaction[];
}

// A mapping for the start day of the week, where Sunday is 0
const WEEK_START = 0; // Sunday

export function CalendarView({ startingBalance, transactions }: CalendarViewProps) {
  const [currentDate, setCurrentDate] = React.useState(new Date());
  const [view, setView] = React.useState('month'); // 'month' or 'week'

  const handlePrev = () => {
    setCurrentDate(view === 'month' ? subMonths(currentDate, 1) : subMonths(currentDate, 1)); // Simplified for now
  };

  const handleNext = () => {
    setCurrentDate(view === 'month' ? addMonths(currentDate, 1) : addMonths(currentDate, 1)); // Simplified for now
  };

  const handleViewChange = (newView: string) => {
    if (newView) setView(newView);
  };

  // --- Calendar Grid Generation ---
  const monthStart = startOfMonth(currentDate);
  const monthEnd = endOfMonth(monthStart);
  const startDate = startOfWeek(monthStart, { weekStartsOn: WEEK_START });
  const endDate = endOfWeek(monthEnd, { weekStartsOn: WEEK_START });
  const days = eachDayOfInterval({ start: startDate, end: endDate });

  const weekDays = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];

  return (
    <div className="flex flex-col h-full w-full">
      {/* Header Controls */}
      <div className="flex items-center justify-between mb-4">
        <div className="flex items-center gap-2">
          <Button variant="outline" size="icon" onClick={handlePrev}>
            <ChevronLeft className="h-4 w-4" />
          </Button>
          <h2 className="text-xl md:text-2xl font-bold text-foreground">
            {format(currentDate, 'MMMM yyyy')}
          </h2>
          <Button variant="outline" size="icon" onClick={handleNext}>
            <ChevronRight className="h-4 w-4" />
          </Button>
        </div>
        <ToggleGroup type="single" value={view} onValueChange={handleViewChange}>
          <ToggleGroupItem value="month">Month</ToggleGroupItem>
          <ToggleGroupItem value="week">Week</ToggleGroupItem>
        </ToggleGroup>
      </div>

      {/* Calendar Grid */}
      <div className="grid grid-cols-7 flex-1 border-t border-l border-border">
        {/* Weekday Headers */}
        {weekDays.map(day => (
          <div key={day} className="p-2 text-center font-semibold text-muted-foreground border-b border-r border-border text-sm">
            {day}
          </div>
        ))}

        {/* Day Cells */}
        {days.map((day, index) => (
          <div 
            key={index}
            className="relative flex flex-col min-h-[150px] p-2 border-b border-r border-border bg-background hover:bg-muted/50 transition-colors duration-150"
          >
            <span className="font-semibold text-sm">
              {format(day, 'd')}
            </span>
            {/* Transaction cards will go here */}
          </div>
        ))}
      </div>
    </div>
  );
}
